import React, { Fragment } from 'react';
import './Docs.less';
import Header from './Header';

const req = require.context('../src', true, /\.docs\.js$/);
const loadDocs = () => req.keys().map(filename => req(filename));

const Docs = () => (
  <Fragment>
    <Header />
    {loadDocs().map(component => <component.default key={component.default.name} />)}
  </Fragment>
);

export default Docs;



